-markdown table td,.blog-post-markdown table tr{transition:border .5s ease,background .5s ease}.blog-post-markdown input[type=checkbox]{position:relative;border:2px solid var(--primary-color);transition:border .5s ease;border-radius:2px;background:none;cursor:pointer;line-height:0;margin:0 .4em 0 0;outline:0;padding:0 !important;vertical-align:text-top;height:20px;width:20px;-webkit-appearance:none}.blog-post-markdown input[type=checkbox]:checked{background-color:var(--primary-color);transition:background-color .5s ease,border .5s ease}.blog-post-markdown input[type=checkbox]:checked:before{content:"";position:absolute;right:50%;top:50%;width:4px;height:10px;border:solid var(--bg-color);transition:border .5s ease;border-width:0 2px 2px 0;margin:-1px -1px 0 -1px;-webkit-transform:rotate(45deg) translate(-50%, -50%);transform:rotate(45deg) translate(-50%, -50%);z-index:2}@media screen and (max-width: 800px){.blog-post-markdown p{font-size:14px}.blog-post-markdown li{font-size:14px}.blog-post-markdown th,.blog-post-markdown td{padding:4px 8px !important;font-size:14px}.blog-post-markdown input[type=checkbox]{width:16px;height:16px;margin-top:2px}.blog-post-markdown blockquote{margin-left:13px}.blog-post-markdown blockquote:before{border-left:3.5px solid var(--font-color);left:-13px}.blog-post-markdown .codeElementDark aside,.blog-post-markdown .codeElementLight aside{padding:.85em !important;line-height:1.33 !important}.blog-post-markdown .codeElementDark aside code,.blog-post-markdown .codeElementLight aside code{line-height:1.33 !important}.blog-post-markdown .codeElementDark aside code span,.blog-post-markdown .codeElementLight aside code span{font-size:13px !important}}